import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import {BrowserRouter as Router, Switch,Route, Link, NavLink} from "react-router-dom";
import {Users} from "./Users"
import {Acerca} from "./Acerca"
import {Contact} from "./Contact";
export function Nuevo () {
    return (
        
         
         <Router>
             
             <div className="mr-auto mt-2  ">
       <div>
     <nav class="navbar navbar-dark bg-dark">
     <Link to="/Users" class="nav-link">inicio</Link>
     <Link to="/Acerca" class="nav-link">Sinopsis</Link>
     <Link to="/Contact" class="nav-link">Contactenos</Link>
   
     <div class="uno">
     <form class="form-inline mr-sm2 bg-dark">
    <input class="form-control mr-sm2" type="search bg-dark" placeholder="" aria-label="Search"/>
             <button class="btn btn-info"type="submit">Buscar</button>
          </form>
          </div>
      </nav>
      </div>
        </div>
        
        <Switch>

       
        <Route path="/Users" exact component={Users} />
        <Route path="/Acerca" exact component={Acerca} />
        <Route path="/Contact" exact component={Contact} />
        
        </Switch>
        </Router>   
        
    );
}
 export default Nuevo;